* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    font-size: 1.2rem;
    width: 100%;
    height: 30px;
    outline: none;
    color: gray;
    border-radius: 15px;
}


ul {
    list-style: none;
}

h1 {
    text-align: center;
}

.card-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.card {
    align-content: center;
    border: 2px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
    padding: 10px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;
    width: 100%;
}